body{
    font-family:'Akrobat', sans-serif;
    background: #F4F4F4;
    color: $black;
}
.admin-bar #page{
	min-height:95.2vh;
}
#page{
	min-height: 100vh;
  display: flex;
  flex-flow: column;
}
.content{
  margin-bottom: 25px;
}
p{
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: $grey;
}

@keyframes leftSlide {
    from {
        left: -110%;
    }
    to {
        left: 0px;
    }
}

@keyframes topSlide {
    from {
        bottom:-50%;
    }
    to {
        bottom: 0px;
    }
}

.img-fluid{
  width:100%;
}
%border-grey{
  border: 1px solid #E2E2E2;
}

.btn-primary{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 50px;
  height: 50px;
  background:$yellow;
  border-radius: 50px;
  font-weight: 900;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: #272727;
  border:none;
  transition: color .4s, background-color .4s;
  &:hover{
    color: #fff;
    background-color:$black;
    text-decoration: none;
  }
}

.btn-stroke{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  height: 50px;
  background:transparent;
  border: 3px solid $yellow;
  border-radius: 50px;
  font-weight: 900;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: $black;
  transition: background-color .4s;
  &:hover{
    color: $black;
    background-color:$yellow;
    text-decoration: none;
  }
}

.content-container{
  padding: 20px;
  @extend %border-grey;
  border-radius: 5px;
  background: #FFFFFF;
}

.section{
  padding-top: 25px;
  padding-bottom: 25px;
}

.section-title{
  margin-bottom: 20px;
  &.text-center{
    h1,h2{
      &::after{
        margin: 0 auto;
        left: 0;
        right: 0;
      }
    }
  }
  h1,h2{
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 10px;
    font-weight: 900;
    font-size: 36px;
    line-height: 43px;
    color: $black;
    &::after{
      position: absolute;
      left: 0;
      bottom: 0;
      content: '';
      width: 100px;
      height: 3px;
      background-color: $yellow;
    }
  }
  .sub-title{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: $grey;
  }
}

.banner{
  padding: 60px 0;
  margin-top: 50px;
  margin-bottom: 25px;
  position: relative;
  &::before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width:90%;
    height: 500px;
    border-radius: 0px 300px 300px 0px;
    background-color:#fff;
    @extend %border-grey;
  }
  .banner-content{
    h1{
      font-weight: 900;
      font-size: 45px;
      line-height: 54px;
      color: $black;
      margin-bottom: 20px;
    }
    p{
      width: 75%;
      font-weight: 500;
      font-size: 20px;
      line-height: 28px;
      color: $grey;
      margin-bottom: 30px;
    }
    .btn{
      width:200px;
      &+.btn{
        margin-left: 25px;
      }
    }
  }
  .banner-display{
    position: relative;
    height: 345px;
    padding-left: 18px;
    padding-right: 17px;
    padding-top: 18px;
    padding-bottom: 96px;
    background-repeat: no-repeat;
    background-image: url(../img/iMac.png);
    background-size: 100%;
    overflow: hidden;
    box-sizing: border-box;
    #content-holder {
        padding: 0;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        text-align: center;
        overflow: hidden;
        background-color: $black;
    }
    #content {
        background-color: #b0b0b0;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        text-align: center;
        overflow: hidden;
        height: 0;
        position: relative;
    }
    &.on #content {
        animation: screen-turn-on .4s linear .5s 1 forwards;
    }
    .power{
      position: relative;
      width: 15px;
      height: 15px;
      padding: 0;
      border: 1px solid red;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      outline: none;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 78px;
      margin: 0 auto;
      background-color: silver;
      &::after{
        content: '';
        position: absolute;
        width: 4px;
        height: 4px;
        margin: auto;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: red;
        border-radius: 50%;
      }
    }
    &.on .power {
        border-color: green;
        &::after{
          background-color: green;
        }
    }
    #text{
        color: #ededed;
        text-align: left;
        font-weight: 200;
        text-transform: uppercase;
        font-size: 3vw;
        animation: shiver 1s linear .5s infinite;
    }
  }
}

.about{
  .content-container{
    margin-top: 30px;
    .section-title{
      margin-bottom: 30px;
    }
    p{
      &:last-of-type{
        margin-bottom: 30px;
      }
    }
  }
}

.photo-block{
  position: relative;
  padding-bottom: 26.5px
}

.social-block{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 25px;
  border: 1px solid #E2E2E2;
  border-radius: 30px;
  background: #FFFFFF;
  .social-title{
    font-weight: 900;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
  }
  .social-list{
    margin: 0;
    margin-left: 25px;
    padding: 0;
    list-style: none;
    display: flex;
    li{
      &+li{
        margin-left: 15px;
      }
      a{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 35px;
        height: 35px;
        background: #F0F0F0;
        @extend %border-grey;
        border-radius: 50%;
        font-size: 18px;
        line-height: 21px;
        color: #656565;
        transition: color .4s, background-color .4s, border-color .4s;
        svg{
          transition: fill .4s;
          fill:#656565;
        }
        &:hover{
          color: $black;
          background-color: $yellow;
          text-decoration: none;
          border-color: $yellow;
          svg{
            fill:$black;
          }
        }
      }
    }
  }
}

.skill-block{
  margin-top: 30px;
  padding: 20px 15px;
  border: 1px solid #E2E2E2;
  border-radius: 5px;
  background: #FFFFFF;
  .skill-diagramm{
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 65px;
    height: 65px;
    background: #F0F0F0;
    color: $grey;
    border-radius: 50%;
    &::before{
      z-index: -1;
      content: '';
      position: absolute;
      top: auto;
      left: auto;
      margin: auto;
      width: 55px;
      height: 55px;
      background: #F0F0F0;
      border-radius: 50%;
      border:5px solid #fff;
    }
    .skill-logo{
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .skill-head{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    h4{
      margin-bottom: 0;
      margin-left: 20px;
      font-weight: 900;
      font-size: 25px;
      line-height: 30px;
      text-transform: uppercase;
      color: $black;
    }
  }
  .skill-info{
    p{
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      letter-spacing: -0.05em;
    }
  }
}

.tool-item{
  text-align: center;
  margin-top: 30px;
  &:hover{
    .tool-item-logo{
      filter: grayscale(0%);
    }
  }
  .tool-item-logo{
    margin-bottom: 20px;
    filter: grayscale(150%);
    transition: filter .4s;
    img{
      max-height: 50px;
      object-fit: contain;
    }
  }
  .tool-item-title{
    font-size: 20px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    color: $grey;
  }
}

.funny-facts{
  margin: 25px 0;
  padding: 65px 0;
  color:#fff;
  text-align: center;
  background: $yellow;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.1);
}

.funny-facts-item{
  .funny-facts-count{
    margin-bottom: 5px;
    font-weight: 900;
    font-size: 36px;
    line-height: 43px;
  }
  .funny-facts-title{
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    text-transform: capitalize;
  }
}

.portfolio{
  .col-lg-4{
    .project-item{
      margin-top: 30px;
    }
  }
}

.project-item{
  position: relative;
  margin-top: 20px;
  border: 1px solid #E2E2E2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  &:hover{
      .project-item-info{
          top: 0;
      }
      .project-item-head{
          animation-duration: .5s;
          animation-name: leftSlide;
          animation-delay: .4s;
          animation-timing-function: ease-in-out;
          animation-fill-mode:forwards;
      }
      .project-item-permalink{
          animation-duration: .5s;
          animation-name: topSlide;
          animation-delay: .9s;
          animation-timing-function: ease-in-out;
          animation-fill-mode:forwards;
      }
  }
  .project-item-thumbnail{
    img{
      width:100%;
      height: auto;
    }
  }
  .project-item-info{
    overflow: hidden;
    position: absolute;
    top: -100%;
    left: 0;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    padding:20px;
    width:100%;
    height: 100%;
    background: rgba(39, 39, 39, 0.9);
    transition: top .4s ease-in-out;
    .project-item-head{
      position: relative;
      left: -110%;
    }
    h3{
      margin-bottom: 5px;
      font-weight: 800;
      font-size: 20px;
      line-height: 25px;
      text-transform: uppercase;
      color: #fff;
    }
    .project-item-terms{
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      text-transform: uppercase;
      color: #F0F0F0;
      a{
        color: #F0F0F0;
        &:hover{
          text-decoration: none;
          color:$yellow;
        }
      }
    }
    .project-item-permalink{
      margin-left: auto;
      position: relative;
      bottom: -50%;
      a{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        background: #FFFFFF;
        font-size: 15px;
        line-height: 23px;
        color: $yellow;
        border-radius: 50%;
        transition: color .4s, background-color .4s;
        &:hover{
          text-decoration: none;
          color: $black;
          background-color: $yellow;
        }
        &+a{
          margin-left: 10px;
        }
      }
    }
  }
}

.contacts{
  margin-top: 25px;
  margin-bottom: 25px;
  position: relative;
  &::before{
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width:90%;
    height: 100%;
    border-radius: 300px 0 0 300px;
    background-color:#fff;
    @extend %border-grey;
  }
}
.contact-form{
  margin-top: 30px;
}

.input{
  margin-bottom: 30px;
  position: relative;
  &::before{
    display: flex;
    align-items: center;
    position: absolute;
    font-family: "Font Awesome 5 Free";
    font-style: normal;
    font-weight: normal;
    color:$grey;
    top: 0;
    bottom: 0;
    margin: auto 0;
    left: 35px;
    z-index: 1;
  }
  &.name{
    &::before{
      content: "\f007";
    }
  }
  &.email{
    &::before{
      content: "\f0e0";
    }
  }
  &.topic{
    &::before{
      content: "\f15b";
    }
  }
}

.textarea{
  margin-bottom: 30px;
  position: relative;
  &::before{
    display: flex;
    align-items: center;
    position: absolute;
    font-family: "Font Awesome 5 Free";
    font-style: normal;
    font-weight: normal;
    color:$grey;
    top: 15px;
    left: 35px;
    z-index: 1;
  }
  &.message{
    &::before{
      content: "\f27a";
    }
  }
}

input{
  width:100%;
  padding: 10px 20px 10px 45px;
  background: #F4F4F4;
  border: 1px solid #E2E2E2;
  box-sizing: border-box;
  border-radius: 50px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  color: $grey;
  outline: none;
  &:focus{
    border-color: $yellow;
  }
  &[type=submit]{
    width: auto;
  }
}

textarea{
  width:100%;
  height: 280px;
  padding: 15px 20px 15px 45px;
  background: #F4F4F4;
  border: 1px solid #E2E2E2;
  box-sizing: border-box;
  border-radius: 20px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  color: $grey;
  outline: none;
  resize: none;
  &:focus{
    border-color: $yellow;
  }
}

/* Project-single Page */
.project-single{
  padding: 50px 0;
  .carousel-item{
      height: 425px;
      iframe{
        width: 100%;
        height: 100%;
        border: none;
      }
      .desktop{
          width: 100%;
          height: 100%;
          margin: 0 auto;
          padding-left: 22px;
          padding-right: 21px;
          padding-top: 21px;
          padding-bottom: 123px;
          background-repeat: no-repeat;
          background-image: url(../img/iMac.png);
          background-size: 100%;
          overflow: hidden;
          box-sizing: border-box;
      }
      .tablet{
          box-sizing: border-box;
          width: 299px;
          height: 100%;
          margin: 0 auto;
          padding: 38px 19px 39px 18px;
          background-size: 100%;
          background-repeat: no-repeat;
          background-image: url(../img/iPad.png);
      }
      .mobile{
          box-sizing: border-box;
          width: 207px;
          height: 100%;
          margin: 0 auto;
          padding: 53px 14px 53px 14px;
          background-size: 100%;
          background-repeat: no-repeat;
          background-image: url(../img/iPhone.png);
      }
      .display{
          width: 100%;
          height: 100%;
          position: relative;
          overflow: hidden;
          img{
              position: absolute;
          }
      }
  }
  .carousel-indicators{
      position: static;
      text-align: center;
      margin: 20px auto;
      li{
          display: inline-block;
          width: 40px;
          height: 5px;
          border-radius: 10px;
          background-color: $grey;
          transition: background-color .5s;
          cursor: pointer;
          &+li{
            margin-left: 5px;
          }
          &:hover{
              background-color: $black;
          }
          &.active{
              background-color: $black;
          }
      }
  }
}

.project-meta{
  display: flex;
  padding-top: 10px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #E2E2E2;
  .project-meta-block{
    &+.project-meta-block{
      margin-left: 60px;
    }
    .project-meta-title{
      font-weight: 800;
      font-size: 16px;
      line-height: 19px;
      text-transform: uppercase;
      color: $black;
    }
    .project-meta-info{
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      color: $grey;
    }
  }
}
.project-desc{
  margin-bottom: 20px;
}

.project-navigation{
  display: flex;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid #E2E2E2;
  border-radius: 5px;
  .nav-item{
    display: flex;
    align-items: center;
    text-decoration: none;
    &:hover{
      .nav-image{
        &::before{
          opacity: 1;
        }
      }
      .nav-info{
        .nav-title{
          color: $yellow;
        }
        .nav-meta{
          color: $black;
        }
      }
    }
    &.project-prev{
      .nav-image{
        &::before{
          content: "\f060";
        }
      }
      .nav-info{
        margin-left: 15px;
      }
    }
    &.project-next{
      margin-left: auto;
      .nav-image{
        &::before{
          content: "\f061";
        }
      }
      .nav-info{
        text-align: right;
        margin-right: 15px;
      }
    }
    .nav-image{
      position: relative;
      width: 80px;
      height: 80px;
      box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      &::before{
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(39, 39, 39, 0.5);
        font-family: "Font Awesome 5 Free";
        font-style: normal;
        font-weight: 900;
        font-size: 30px;
        line-height: 34px;
        color: #fff;
        opacity: 0;
        transition: opacity .4s;
      }
      img{
        height: 100%;
        object-fit: cover;
      }
    }
    .nav-info{
      .nav-title{
        margin-bottom: 5px;
        font-weight: 900;
        font-size: 20px;
        line-height: 24px;
        color: $black;
        transition: color .4s;
      }
      .nav-meta{
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: $grey;
        transition: color .4s;
      }
    }
  }
}

/* animations */
@keyframes screen-turn-on {
    0% {
        width: 0%;
        height: 1px;
        border-radius: 0px;
        background-color: #b0b0b0;
    }
    50% {
        width: 100%;
        height: 1px;
        border-radius: 0px;
        background-color: #b0b0b0;
    }
    51% {
        width: 100%;
        height: 1px;
        border-radius: 0px;
        background-color: #b0b0b0;
    }
    70% {
        width: 100%;
        height: 1px;
        border-radius: 0px;
        background-color: #b0b0b0;
    }
    80% {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        background-color: #b0b0b0;
    }
    100% {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        background-color: #454545;
    }
}
@keyframes shiver {
    0%   {  transform: translate(1px, 1px); text-shadow: 6px 0 rgba(255,0,0,0.9), -6px 0 rgba(0,0,255,0.9);  }
    5%   {  transform: translate(1px, 1px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    10%  {  transform: translate(1px, 1px); text-shadow: 5px 0 rgba(255,0,0,0.9), -5px 0 rgba(0,0,255,0.9);  }
    15%  {  transform: translate(1px, 1px); text-shadow: 7px 0 rgba(255,0,0,0.9), -7px 0 rgba(0,0,255,0.9);  }
    20%  {  transform: translate(1px, 2px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    25%  {  transform: translate(1px, 2px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    30%  {  transform: translate(1px, 2px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    35%  {  transform: translate(1px, 2px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    40%  {  transform: translate(2px, 1px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    45%  {  transform: translate(2px, 1px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    50%  {  transform: translate(2px, 1px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    55%  {  transform: translate(2px, 1px); text-shadow: 4px 0 rgba(255,0,0,0.9), -4px 0 rgba(0,0,255,0.9);  }
    60%  {  transform: translate(1px, 1px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    65%  {  transform: translate(1px, 1px); text-shadow: 5px 0 rgba(255,0,0,0.9), -5px 0 rgba(0,0,255,0.9);  }
    70%  {  transform: translate(1px, 1px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    75%  {  transform: translate(1px, 1px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    80%  {  transform: translate(2px, 1px); text-shadow: 5px 0 rgba(255,0,0,0.9), -5px 0 rgba(0,0,255,0.9);  }
    85%  {  transform: translate(2px, 1px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
    90%  {  transform: translate(2px, 2px); text-shadow: 5px 0 rgba(255,0,0,0.9), -5px 0 rgba(0,0,255,0.9);  }
    95%  {  transform: translate(2px, 2px); text-shadow: 2px 0 rgba(255,0,0,0.9), -2px 0 rgba(0,0,255,0.9);  }
    100% {  transform: translate(1px, 2px); text-shadow: 3px 0 rgba(255,0,0,0.9), -3px 0 rgba(0,0,255,0.9);  }
}
