.breadcrumb-container{
    height: auto;
    background-color: #fff;
    @extend %border-grey;
}
.breadcrumb{
    margin: 0;
    padding: 10px 0;
    list-style: none;
    &>.breadcrumb-item{
        position: relative;
        display: inline;
        margin-left: 0;
        font-size: 12px;
        color: $grey;
        font-weight: 700;
        text-transform: uppercase;
        .current-item,
        a{
            padding-right: 4px;
        }
        &:first-child{
            margin-left: 0;
            a{
                padding-left: 0;
            }
        }
        &:not(:last-child){
            &::after{
                content: '/';
                color: $grey;
            }
        }
        a{
            text-decoration: none;
            color: $grey;
            &:hover{
                color: $yellow;
                span{
                    color: $yellow;
                }
            }
        }
    }
}
