  padding: 10px 0;
  list-style: none; }
  .breadcrumb > .breadcrumb-item {
    position: relative;
    display: inline;
    margin-left: 0;
    font-size: 12px;
    color: #646464;
    font-weight: 700;
    text-transform: uppercase; }
    .breadcrumb > .breadcrumb-item .current-item,
    .breadcrumb > .breadcrumb-item a {
      padding-right: 4px; }
    .breadcrumb > .breadcrumb-item:first-child {
      margin-left: 0; }
      .breadcrumb > .breadcrumb-item:first-child a {
        padding-left: 0; }
    .breadcrumb > .breadcrumb-item:not(:last-child)::after {
      content: '/';
      color: #646464; }
    .breadcrumb > .breadcrumb-item a {
      text-decoration: none;
      color: #646464; }
      .breadcrumb > .breadcrumb-item a:hover {
        color: #F4D03F; }
        .breadcrumb > .breadcrumb-item a:hover span {
          color: #F4D03F; }

.comments-pagination {
  margin-bottom: 15px; }

.pagination,
.comments-pagination {
  margin-top: 50px; }
  .pagination ul,
  .comments-pagination ul {
    margin: 0;
    padding: 0; }
    .pagination ul li,
    .comments-pagination ul li {
      display: inline-block;
      margin: 0 6px; }
    .pagination ul .page-numbers,
    .comments-pagination ul .page-numbers {
      display: inline-block;
      color: #646464;
      background-color: #fff;
      padding: 5px 10px;
      font-size: 14px;
      border: medium none;
      font-weight: 700;
      line-height: 1.5em;
      text-decoration: none;
      transition: background-color .5s linear, color .3s linear; }
      .pagination ul .page-numbers a,
      .comments-pagination ul .page-numbers a {
        color: #646464;
        text-decoration: none; }
      .pagination ul .page-numbers.active,
      .comments-pagination ul .page-numbers.active {
        pointer-events: none;
        background-color: #F4D03F;
        color: #fff; }
      .pagination ul .page-numbers.current,
      .comments-pagination ul .page-numbers.current {
        background-color: #F4D03F;
        color: #fff; }
      .pagination ul .page-numbers:first-child,
      .comments-pagination ul .page-numbers:first-child {
        margin: 0; }
    .pagination ul a.page-numbers,
    .comments-pagination ul a.page-numbers {
      cursor: pointer; }
      .pagination ul a.page-numbers:hover,
      .comments-pagination ul a.page-numbers:hover {
        background-color: #F4D03F;
        color: #fff; }

