.comments-pagination{
    margin-bottom: 15px;
}
.pagination,
.comments-pagination{
    margin-top: 50px;
    ul{
        margin: 0;
        padding: 0;
        li{
            display: inline-block;
            margin: 0 6px;
        }
        .page-numbers{
            display: inline-block;
            color:$grey;
            background-color: #fff;
            padding: 5px 10px;
            font-size: 14px;
            border: medium none;
            font-weight: 700;
            line-height: 1.5em;
            text-decoration: none;
            @extend %border-grey;
            transition: background-color .5s linear, color .3s linear;
            a{
                color:$grey;
                text-decoration: none;
            }
            &.active{
                pointer-events: none;
                background-color: $yellow;
                color: #fff;
            }
            &.current{
                background-color: $yellow;
                color: #fff;
            }
            &:first-child{
              margin: 0;
            }
        }
        a.page-numbers{
            cursor: pointer;
            &:hover{
                background-color: $yellow;
                color:#fff;
            }
        }
    }
}
