.header{
  background-color: $black;
  padding: 30px 0;
}

.navbar{
  padding: 0;
  justify-content: flex-end;
  .navbar-nav{
    padding: 0;
    margin: 0;
    list-style:none;
    .nav-item{
      display: inline-block;
      &:first-child{
        .nav-link{
          padding-left: 0;
        }
      }
    }
    .nav-link{
      font-weight: 900;
      font-size: 18px;
      line-height: 22px;
      text-transform: uppercase;
      color: #FFFFFF;
      text-decoration:none;
      transition: color .4s;
      &:hover{
        color:$yellow;
      }
    }
  }
}

.navbar-toggler{
  color: #fff;
  border-color: #fff;
  outline: none;
}

@media screen and(max-width:768px){
  .navbar-nav{
    .nav-item{
      margin: 10px 0;
      text-align: center;
    }
  }
}
