.footer{
  margin-top: auto;
  background-color: $black;
  .top-footer{
      padding: 35px 0;
  }
  .navigation{
    .menu{
      li{
        a{
          color: $grey;
          &:hover{
            color: $yellow;
          }
        }
      }
    }
  }
  .social-list{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    li{
      &+li{
        margin-left: 15px;
      }
      a{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 35px;
        height: 35px;
        background: $grey;
        border-radius: 50%;
        font-size: 18px;
        line-height: 21px;
        color: $black;
        transition: color .4s, background-color .4s, border-color .4s;
        svg{
          transition: fill .4s;
          fill:$black;
        }
        &:hover{
          color: $black;
          background-color: $yellow;
          text-decoration: none;
          svg{
            fill:$black;
          }
        }
      }
    }
  }
}
.footer-copyright{
  height: 30px;
  background: #343435;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  text-transform: capitalize;
  color: #8E999B;
}
