@media screen and(max-width:992px){
  .banner{
    &::before{
      margin: 0;
      border-radius: 0;
      width: 100%;
    }
    .banner-content{
      text-align: center;
      margin-bottom: 50px;
      p{
        width: 100%;
      }
    }
  }
  .photo-block{
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .skill-block{
    text-align: center;
    .skill-head{
      flex-flow: column;
      justify-content: center;
      h4{
        margin: 0;
        margin-top: 10px;
      }
    }
  }
}

@media screen and(max-width:768px){
  .funny-facts-item{
    &+.funny-facts-item{
      margin-top: 20px;
    }
  }
  .project-item{
    margin-top: 30px;
  }
  .contacts{
    &::before{
      width: 100%;
      border-radius: 0;
    }
  }
  .footer{
    .social-list{
      justify-content: center;
    }
  }
}

@media screen and(max-width:576px){
  .banner{
    .banner-content{
      display: flex;
      flex-flow: column;
      align-items: center;
      .btn{
        &+.btn{
          margin-left: 0;
          margin-top: 20px;
        }
      }
    }
  }
  .social-block{
    .social-list{
      justify-content: center;
      margin: 0;
    }
  }
}


@media screen and(max-width:1199px){
    .project-single{
        .carousel-item{
            height: 408px;
            .desktop{
                padding: 22px 21px 112px;
            }
            .tablet{
                width: 286px;
                padding: 37px 19px 39px 18px;
            }
            .mobile{
                width: 198px;
                padding: 52px 14px 54px 14px;
            }
        }
    }
}

@media screen and(max-width:991px){
    .project-single{
        .carousel-indicators{
            bottom: -15px;
        }
        .carousel-item{
            height: 530px;
            .desktop{
              padding: 29px 28px 145px;
            }
            .tablet{
              width: 54%;
              height: 100%;
              padding: 47px 25px 51px 22px;
            }
            .mobile{
              height: 100%;
              width: 37.4%;
              padding: 67px 19px 69px 18px;
            }
        }
        .project-description{
            margin: 30px 0;
        }
    }
}

@media screen and(max-width:767px){
    .project-single{
        .carousel-item{
            height: 393px;
            .desktop{
                width: 510px;
                padding: 22px 21px 108px;
            }
            .tablet{
                width: 275px;
                padding: 35px 18px 38px 17px;
            }
            .mobile{
                width: 191px;
                padding: 50px 13px 52px 14px;
            }
        }
    }
}

@media screen and(max-width:575px){
    .project-single{
        .carousel-item{
            height: 225px;
            .desktop{
                width: 290px;
                padding: 12px 12px 63px;
            }
            .tablet{
                width: 157px;
                padding: 20px 11px 23px 10px;
            }
            .mobile{
                width: 110px;
                padding: 29px 8px 29px 8px;
            }
        }
    }
    .project-meta{
      .project-meta-block{
        &+.project-meta-block{
          margin-left: 20px;
        }
      }
    }
    .project-navigation{
      flex-flow: column;
      align-items: unset;
      border: none;
      background: none;
      .nav-item{
        background: #fff;
        border: 1px solid #e2e2e2;
        border-radius: 5px;
        &+.nav-item{
          margin-top: 20px;
          margin-left: 0;
        }
        &.project-next{
          justify-content: flex-end;
          .nav-info{
            margin-left: 15px;
          }
        }
        &.project-prev{
          .nav-info{
            margin-right: 15px;
          }
        }
      }
    }
}
